import { getHex } from "pastel-color";

function Tag({ text, k, remove }) {
  return (
    <div
      style={{
        borderRadius: 3,
        margin: 4,
        padding: "1px 8px",
        display: "inline-block",
        backgroundColor: getHex(text),
        cursor: "pointer",
        fontWeight: 600,
        fontSize: "1.2em",
      }}
      onClick={() => {
        remove(k);
      }}
    >
      {text}
    </div>
  );
}

function Tags({ tags, remove }) {
  return (
    <div style={{ marginBottom: 16 }}>
      {tags.map((t) => (
        <Tag text={t.text} key={t.key} k={t.key} remove={remove} />
      ))}
    </div>
  );
}

export default Tags;
