import { useState } from "react";
import { Input, Button } from "antd";
import { ClearOutlined } from "@ant-design/icons";
import Tags from "./Tags";
import { getHex } from "pastel-color";
import "./App.css";

const { Search } = Input;

function App() {
  const [tags, setTags] = useState([]);
  const [value, setValue] = useState("");

  const onEnter = (v) => {
    if (!v) return;
    const t = {
      text: v,
      key: Date.now(),
    };
    setTags([...tags, t]);
    setValue("");
  };

  const remove = (k) => {
    setTags([...tags.filter(({ key }) => key !== k)]);
  };

  return (
    <div className="App">
      <header className="App-header">
        <a
          style={{ color: "white" }}
          href="https://github.com/Yzhibin/pastel-color"
          target="_blank"
          rel="noreferrer noopener"
        >
          Pastel Color
        </a>
      </header>
      <div className="main">
        <Search
          placeholder="write a poem"
          allowClear
          enterButton={
            <Button
              style={{
                backgroundColor: getHex("OK"),
                borderRadius: "0 2px 2px 0",
              }}
              size="large"
            >
              OK
            </Button>
          }
          size="large"
          value={value}
          onChange={(e) => {
            setValue(e.target.value);
          }}
          onSearch={onEnter}
          style={{ marginBottom: 16 }}
        />

        <Tags tags={tags} remove={remove} />
        {tags.length !== 0 && (
          <Button
            shape="circle"
            style={{ backgroundColor: getHex() }}
            onClick={() => {
              setTags([]);
            }}
          >
            <ClearOutlined />
          </Button>
        )}
      </div>
    </div>
  );
}

export default App;
